// Resolve sprite URL correctly for both dev and production (same as render-functions.js)
const spriteBase = new URL('../img/sprite.svg', import.meta.url).href;

(function () {
  const btn = document.querySelector('.header-btn');
  const menu = document.querySelector('.header-menu');

  if (!btn || !menu) return;

  const useEl = btn.querySelector('use');
  const ICON_OPEN = '#icon-exit';
  const ICON_CLOSE = '#icon-menu-burger';

  let overlay = null;
  let originalParent = menu.parentNode;
  let originalNextSibling = menu.nextSibling;

  function setIcon(id) {
    if (!useEl) return;
    const path = spriteBase + id;
    useEl.setAttribute('href', path);
    useEl.setAttributeNS('http://www.w3.org/1999/xlink', 'xlink:href', path);
  }

  function createOverlay() {
    overlay = document.createElement('div');
    overlay.className = 'header-overlay';
    // –î–æ–¥–∞—î–º–æ —Å—Ç–∏–ª—ñ
    Object.assign(overlay.style, {
      position: 'fixed',
      top: '0',
      left: '0',
      right: '0',
      bottom: '0',
      background: '#060307',
      zIndex: '999',
      overflow: 'auto',
      display: 'none',
    });
    document.body.appendChild(overlay);

    // –ó–∞–∫—Ä–∏—Ç—Ç—è –ø—Ä–∏ –∫–ª—ñ–∫—É –Ω–∞ –ø–æ—Å–∏–ª–∞–Ω–Ω—è –≤ –º–µ–Ω—é
    menu.addEventListener('click', e => {
      if (e.target.closest('a[href^="#"]')) {
        closeOverlay();
      }
    });
  }

  function openOverlay() {
    if (!overlay) createOverlay();

    overlay.style.display = 'block';
    document.body.style.overflow = 'hidden';
    overlay.appendChild(menu); // –ü–µ—Ä–µ–º—ñ—â—É—î–º–æ –º–µ–Ω—é –≤ –æ–≤–µ—Ä–ª–µ–π

    menu.classList.add('is-open');
    btn.setAttribute('aria-expanded', 'true');
    setIcon(ICON_OPEN);
    document.addEventListener('keydown', onKeyDown);
  }

  function closeOverlay() {
    if (!overlay || overlay.style.display === 'none') return;

    overlay.style.display = 'none';
    document.body.style.overflow = '';

    // –ü–æ–≤–µ—Ä—Ç–∞—î–º–æ –º–µ–Ω—é –Ω–∞ –º—ñ—Å—Ü–µ –≤ DOM
    originalParent.insertBefore(menu, originalNextSibling);

    menu.classList.remove('is-open');
    btn.setAttribute('aria-expanded', 'false');
    setIcon(ICON_CLOSE);
    document.removeEventListener('keydown', onKeyDown);
  }

  function onKeyDown(e) {
    if (e.key === 'Escape') closeOverlay();
  }

  // –ì–æ–ª–æ–≤–Ω–∏–π –æ–±—Ä–æ–±–Ω–∏–∫ –ø–æ–¥—ñ—ó
  btn.addEventListener('click', e => {
    e.preventDefault();
    const isOpen = btn.getAttribute('aria-expanded') === 'true';
    if (isOpen) {
      closeOverlay();
    } else {
      openOverlay();
    }
  });
})();
